import { Typography, Box, Grid, ToggleButton } from '@mui/material'
import React from 'react'
import StyledButton from '../StyledButton'

function Piece(props){
  const piece = ["J'ai dj ma pice", "Je ne l'ai pas"]
  if (props.values.need === "J'ai besoin d'aide pour remplacer ma pice") {
    return (
      <Box>
        <Typography
          variant="h2"
          color="primary"
          align="left"
          mb={3}
          mt={3}
        >
          Possdez vous la nouvelle pice ?
        </Typography>

       <StyledButton label={piece} handleChange={props.handleChange} width={300} state={"piece"}/>
        <p>*Nous vous ferons parvenir un lien en fin de parcours pour acheter votre pice</p>
      </Box>
    )
  }
  return <></>
}

export default Piece
